/* -- GLOBAL -- */
html {
	box-sizing: border-box;
	overflow-y: scroll;
}

*, *:before, *:after {
	box-sizing: inherit;
}

body {
	height: 100%;
}

/* -- Skip links -- */
.hide-skip {
	margin: 0;
}

.hide-skip a {
	color: black;
	position: absolute;
	overflow: hidden;
	height: 1px;
	z-index: 9999;
}

.hide-skip a:focus {
	height: auto;
}

/* -- Visually hidden */
.visuallyhidden {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}

/* -- layout -- */
.site-wrapper {
	max-width: 1400px;
	margin: 0 auto 2em auto;
	padding: 0 1em;
}

/* -- Links -- */
a {
	color: black;
	text-decoration: none;
}

a:hover {
	text-decoration: underline;
}

/* -- Base Typography -- */
body {
	line-height: 1.2em;
	font-family: "Libre Franklin", "Source Sans Pro", Verdana, sans-serif;
	font-weight: 200;
	font-size: 1rem;
}

@media screen and (min-width: 425px) {
	.site-wrapper {
		padding: 0 2em;
	}
}

@media screen and (min-width: 800px) {
	body {
		font-size: 1.125rem;
	}
}

.header {
	display: flex;
	flex-direction: column;
}

.header ul {
	list-style-type: none;
}

.header__top {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 2em;
}

.header__nav {
	margin: 1em 0 0 -1em;
	display: none;
	width: 100vw;
	background-color: #f3f3f3;
}

@media screen and (min-width: 425px) {
	.header__nav {
		margin: 1em 0 0 -2em;
	}
}

/* -- Logo -- */
.logo {
	width: 100%;
	height: 100%;
	padding-bottom: 10%;
	margin: 0;
}

@media screen and (min-width: 425px) {
	.logo {
		padding-bottom: 0;
		width: 311px;
		height: 38px;
	}
}

/* -- Menu -- */
.button__menu {
	height: 32px;
	width: 32px;
	padding: 0;
	margin-left: 2em;
	border: 0;
	order: 3;
	background-image: url("../img/icons/menu.svg");
	background-repeat: no-repeat;
	background-color: transparent;
}

/* -- About/contact -- */
.main-nav__about-contact {
	display: flex;
	flex-direction: column;
	font-size: 1.125em;
	padding: 1em 0;
	text-align: center;
	border-bottom: 1px dashed grey;
	border-top: 1px dashed grey;
	margin-bottom: 2em;
	width: 100%;
	list-style-type: none;
}

.main-nav__about-contact ul {
	padding: 0;
}

.main-nav__about-contact .about-contact__item + * {
	margin-top: 1em;
}

.about-contact {
	display: none;
}

/* -- Main Nav -- */
.main-nav {
	padding: 2em 0;
}

.main-nav__items {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 0;
	padding: 0;
}

.main-nav__item + * {
	margin-top: 2em;
}

.main-nav__link {
	display: block;
	position: relative;
}

.main-nav__link--home {
	background-position: -5px -45px;
	width: 40px;
	height: 28px;
}

.main-nav__link--home.current {
	background-position: -263px -45px;
}

@media screen and (min-width: 800px) {
