.parent-item {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	position: relative;
	width: 100%;
}

.parent-icon {
	margin-left: .3em;
	position: absolute;
	left: 100%;
	bottom: 35%;
}

.sub-menu {
	margin-top: 1em;
	padding: 0;
	text-align: center;
	display: none;
	width: 100%;
}

.sub-menu__item + * {
	border-top: 1px dashed lightgrey;
}

.sub-menu__link {
	display: block;
	padding: 1em;
}

.sub-menu__link:hover {
	text-decoration: none;
	background-color: #f3f3f3;
}

.menu-displayed {
	display: flex;
	flex-direction: column;
}

.parent-icon {
	transform: rotate(0);
	transition: transform 300ms;
}

.parent-icon.rotate {
	transform: rotate(180deg);
}

@media screen and (min-width: 720px) {
	.about-contact {
		display: flex;
		margin-top: 1em;
		font-size: 1em;
		margin: 0;
		padding: 0;
	}
	.about-contact__link.current, .about-contact__link:hover {
		border-bottom: 1px solid black;
		text-decoration: none;
	}
	.about-contact__item:first-child::after {
		content: "|";
		margin: 0 2.5em;
		color: grey;
	}
	.main-nav__about-contact {
		display: none;
	}
	.button__menu {
		margin-left: 0;
	}
}

@media screen and (min-width: 800px) {
	.header {
		margin-top: 1em;
	}
	.header__nav {
		background-color: transparent;
		width: 100%;
		margin: 1em 0 0 0;
	}
	.button__menu {
		display: none;
	}
	.about-contact {
		display: flex;
	}
	.header__nav {
		display: block;
	}
	.main-nav {
		padding: 0;
	}
	.main-nav__items {
		flex-direction: row;
		justify-content: space-between;
	}
	.main-nav__item {
		margin-right: 15px;
	}
	.main-nav__item + * {
		margin-top: 0;
	}
	.main-nav__item:last-child {
		margin-bottom: 0;
	}
	.sub-menu {
		position: absolute;
		top: 110%;
		font-size: .8em;
		z-index: 99999;
		background: white;
		width: 150%;
		left: -22%;
		border: 1px dashed grey;
		border-top: 0;
	}
	.parent-item {
		width: auto;
	}
}

.map {
	margin: 0 auto;
	overflow: scroll;
	overflow: -moz-scrollbars-none;
	-ms-overflow-style: none;
	animation-name: fadeIn;
	background: #f9f7ef;
	animation-duration: 1.5s;
	animation-fill-mode: forwards;
	border: 1px dashed silver;
	border-top: 0;
	height: 600px;
	height: 60vh;
	transition: transform 700ms ease-in-out;
	cursor: move;
	/* fallback: no `url()` support or images disabled */
	cursor: url("assets/img/icons/grab.cur"), move;
	/* fallback: Internet Explorer */
	cursor: grab;
}

@keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

.map::-webkit-scrollbar {
	display: none;
}

.map.dragscroll.grab {
	cursor: url("assets/img/icons/grab.cur"), move;
	cursor: grab;
}

.map.dragscroll.grabbing {
	cursor: url("assets/img/icons/grabbing.cur"), move;
	cursor: grabbing;
	transition: transform 700ms ease-in-out;
	box-shadow: none;
	border: none;
}

.map.dragscroll.grabbing.dragscroll--cancel {
	overflow: hidden;
}

.map__inner {
	position: relative;
	transition: transform 400ms, width 700ms, height 700ms;
	background-image: url("../img/maps/map_today4.png");
	background-repeat: no-repeat;
	background-size: contain;
	width: 924px;
	height: 616px;
	margin: 200px;
	image-rendering: -moz-crisp-edges;
	/* Firefox */
	image-rendering: -o-crisp-edges;
	/* Opera */
	image-rendering: -webkit-optimize-contrast;
	/* Webkit (non-standard naming) */
	image-rendering: crisp-edges;
	-ms-interpolation-mode: nearest-neighbor;
	/* IE (non-standard property) */
}

.map__inner.map__inner--1960s1980s {
	background-image: url("../img/maps/map_1960s1980s3.png");
}

.map__inner.map__inner--1800s {
	background-image: url("../img/maps/map_1800s4.png");
}

.map__inner.map__inner--pre {
	background-image: url("../img/maps/map_pre1800s4.png");
}

