.slider-container {
	border: 1px solid transparent;
	margin-top: 1em;
	padding: 0 2em;
	background: #f3f3f3;
	border-radius: 30px 30px 0 0;
}

.slider {
	position: relative;
	margin: 3em auto;
	max-width: 700px;
}

.ui-slider-horizontal.ui-slider-pips {
	margin-bottom: 3em;
}

.ui-widget-content {
	border-top: 1px dashed black;
}

.ui-slider-horizontal {
	height: .8em;
}

.ui-slider-pips .ui-slider-line {
	background: #f3f3f3;
	border: 2px solid black;
	width: 15px;
	height: 15px;
	border-radius: 50%;
	position: absolute;
	left: 50%;
	top: -28px;
}

.ui-slider-pips .ui-slider-label {
	position: absolute;
	left: 50%;
	margin-left: -1.4em;
	width: auto;
	top: -3px;
	white-space: nowrap;
	color: grey;
	font-size: .8125em;
}

.ui-slider-pips .ui-slider-pip {
	font-size: 1em;
}

.ui-slider-pip-0 .ui-slider-label {
	margin-left: -1.625em;
}

.ui-slider-pip-1 .ui-slider-label {
	margin-left: -0.875em;
}

.ui-slider-pip-2 .ui-slider-label {
	margin-left: -2.25em;
}

.ui-slider-pip-3 .ui-slider-label {
	margin-left: -1em;
}

.ui-slider-pips .ui-slider-pip-initial {
	font-weight: normal;
}

.ui-slider-pips .ui-slider-pip-selected {
	font-weight: 400;
}

.ui-slider-pips .ui-slider-pip-selected .ui-slider-label {
	color: black;
}

.ui-slider-handle {
	position: absolute;
	z-index: 2;
	width: 15px;
	height: 15px;
	top: -8px;
	background: #79deff;
	border-radius: 50%;
	border: 2px solid;
	cursor: pointer;
}

.ui-widget-content .ui-slider-handle.ui-state-hover {
	background: orange;
}

.ui-widget-content .ui-slider-handle.ui-state-active,
.ui-widget-content .ui-slider-handle.ui-state-focus {
	outline: none;
}

.ui-slider-pips:not(.ui-slider-disabled) .ui-slider-pip:hover .ui-slider-label {
	font-weight: normal;
}

@media screen and (min-width: 425px) {
	.slider-container {
		padding: 0 3em;
	}
	.ui-slider-pips .ui-slider-label {
		font-size: .925em;
	}
}

.breadcrumb {
	display: none;
}

/* -- Main -- */
.main {
	display: flex;
	flex-direction: column;
	align-items: center;
}

/* -- Icon -- */
.main-icon {
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.main-icon__img {
	padding: 10vw;
	width: 55vw;
}

.main-text {
	text-align: center;
	max-width: 33.5rem;
	font-size: calc(.8em + 1vw);
	font-weight: 200;
	width: 100%;
}

.main-text__title {
	font-size: calc(1.25em + 2vw);
	font-weight: 300;
	line-height: 1em;
	margin-top: 0;
	max-width: 100%;
}

.main-text__link {
	font-weight: 700;
	text-decoration: none;
	color: #ff3c19;
	border-bottom: 0.08em solid #dedede;
	padding-bottom: 0.04em;
}

.main-text__link:hover {
	text-decoration: none;
	border-bottom-color: #ff3c19;
}

@media screen and (min-width: 425px) {
	.main-text {
		line-height: calc(1em + 1vw);
	}
}

@media screen and (min-width: 800px) {
	/* -- Breadcrumb -- */
	.breadcrumb {
		display: flex;
		align-items: center;
		white-space: nowrap;
		flex-wrap: wrap;
		margin: 2em 0;
		padding: 1em 2em;
		font-size: 0.88888888888888888888888888888889em;
		border-left: 3px solid black;
		background-color: #f6f6f6;
	}
	.breadcrumb__grey {
		color: grey;
		margin: 0;
	}
	.breadcrumb__grey:not(:first-child):before {
		content: ">";
		margin: 0 .5em;
		font-size: 1em;
		font-weight: 200;
	}
	.breadcrumb__link {
		font-weight: bold;
	}
	.main {
		flex-direction: row;
		justify-content: space-between;
		margin-top: 2em;
	}
	.main-icon {
		max-width: 350px;
		margin-right: 2em;
		flex: 1;
	}
	.main-icon__img {
		padding: 0;
		width: 100%;
	}
	.main-text {
		max-width: 30em;
		font-weight: 300;
		font-size: 1.1111111111111111111111111111111em;
		text-align: right;
		flex: 2;
	}
	.main-text__link {
		font-weight: 400;
	}
}

@media screen and (min-width: 1048px) {
	.main-icon {
		width: 100%;
		flex: auto;
	}
	.main-text {
		flex: auto;
	}
}

@media screen and (min-width: 1240px) {
	.main-text {
		font-size: 1.3333333333333333333333333333333em;
		line-height: 1.3em;
	}
	.main-icon {
		transform: rotate(-3deg) translate(10px, -10px);
		border-left: 1em dashed #f3f3f3;
		border-top: 1em dashed #f3f3f3;
	}
}
